import React from 'react'
import PropTypes from 'prop-types'
import Grid from '@material-ui/core/Grid'
import ForecastItem from './../ForecastItem'

const renderForcastItem = forecast => {

    const { weekDay, hour, state, temperature } = ForecastItem
    return (
        //Para listas se debe incorporar una key unica
        <Grid item key={`${weekDay}${hour}`}>
            <ForecastItem  hour={hour} weekDay={weekDay} state={state} temperature={temperature} />
        </Grid>
    )

}

const Forecast = ({ forecastItemList }) => {
    return (
        <>
            <Grid container
                justify="center"
                alignItems="center">
                {
                    forecastItemList.map(forecast => renderForcastItem(forecast))
                }
            </Grid>
        </>
    )
}

Forecast.propTypes = {

    forecastItemList: PropTypes.array.isRequired,

}

export default Forecast
